import React from 'react';
import './App.css';

function PageBar(props) {
  const [getPage, setPage] = React.useState(0);

  React.useEffect(() => {
    setPage(props.Page)
  }, [props.Page])

  const NextPage = (e) => {
    let n = Math.min(getPage + props.PageSize, props.Total - 1)
    let pg = Math.trunc(n / props.PageSize) * props.PageSize
    setPage(pg);

    if (props.OnPage) {
      props.OnPage(pg)
    }
  }

  const PrevPage = (e) => {
    let pg = Math.max(getPage - props.PageSize, 0)
    setPage(pg);

    if (props.OnPage) {
      props.OnPage(pg)
    }
  }

  const GotoPage = (n) => {
    let pg = Math.trunc(n / props.PageSize) * props.PageSize
    setPage(pg);

    if (props.OnPage) {
      props.OnPage(pg)
    }
  }

  let endPage = Math.min(getPage + props.PageSize, props.Total)

  let modals = []
  for (let n = 0; n < props.Total; n += props.PageSize) {
    modals.push(<span className="badge btn text-bg-info me-1"
      data-bs-dismiss="modal"
      onClick={() => { GotoPage(n+1) }}
      >{n + 1}</span>)
  }

  let items = []

  items.push(
    <div key={items.length} className="modal fade" id="pageModal" tabindex={-1} aria-hidden={true}>
      <div className="modal-dialog">
        <div className="modal-content">
          <div className="modal-body">
            {modals}
          </div>
        </div>
      </div>
    </div>)

  items.push(<span className="mx-1" key={items.length}>
    <button className="btn btn-light" onClick={() => { GotoPage(0) }}>|&lt;</button>
  </span>)

  items.push(<span className="mx-1" key={items.length}>
    <button className="btn btn-light" onClick={PrevPage}>&lt;</button>
  </span>)

  items.push(<span className="mx-1" key={items.length}>
    <button className="btn btn-light" data-bs-toggle="modal" data-bs-target="#pageModal">{getPage + 1}-{endPage} / {props.Total}</button>
  </span>)

  items.push(<span className="mx-1" key={items.length}>
    <button className="btn btn-light" onClick={NextPage}>&gt;</button>
  </span>)

  items.push(<span className="mx-1" key={items.length}>
    <button className="btn btn-light" onClick={() => { GotoPage(props.Total - 1) }}>&gt;|</button>
  </span>)

  return (
    <span className="App">
      {items}
    </span>
  );
}

export default PageBar;
